import { login, register } from '../../apis/user'
import { mapMutations, mapState } from 'vuex'
const Cookie = process.client ? require('js-cookie') : undefined
export default {
  name: 'LoginIndex',
  middleware: 'notAuthenticated',
  data() {
    return {
      userForm: {
        username: '',
        email: '',
        password: ''
      },
      errors: []
    }
  },
  computed: {
    ...mapState(['user']),
    isLogin() {
      return this.$route.name === 'login'
    }
  },
  methods: {
    async handLoginOrRegister() {
      const params = {
        username: this.userForm.username,
        email: this.userForm.email,
        password: this.userForm.password
      }
      if (this.userForm.username) {
        await this.handleRegister(params)
      } else {
        await this.handleLogin(params)
      }
    },

    async handleLogin(params) {
      delete params.username
      try {
        const { user } = await login(params)
        this.$store.commit('setUser', user)
        Cookie.set('auth', user)
        this.$router.push('/')
      } catch (error) {
        console.error(error)
        this.errors = []
        const errors = error['errors']
        this.handleErrors(errors)
      }
    },

    async handleRegister(params) {
      try {
        const { user } = await register(params)
        Cookie.set('auth', user)
        this.$store.commit('setUser', user)
        this.$router.push('/')
      } catch (error) {
        console.error(error)
        this.errors = []
        const errors = error['errors']
        this.handleErrors(errors)
      }
    },

    handleErrors(errors) {
      for (const key in errors) {
        errors[key].forEach(item => {
          this.errors.push(key + ' ' + item)
        })
      }
    },

    ...mapMutations(['setAuth'])
  }
}
